<template>
  <div class="content">
    <div class="operation">
    <el-row  :gutter="40">
      <el-col :span="6">
        <el-input>
          <template slot="prepend">店铺分类</template>
        </el-input>
      </el-col>
      <el-col :span="6">
        <el-button type="primary" @click="searchList" icon="el-icon-search">查询</el-button>
        <el-button type="primary" @click="initTree" icon="el-icon-plus">新增</el-button>
      </el-col>
    </el-row>
  </div>
  <div class="table">
    <el-table :data="tableData" border v-loading="loading">
      <el-table-column label="序号" align="center" type="index"></el-table-column>
      <el-table-column label="ID" prop="areaCode" align="center"></el-table-column>
      <el-table-column label="分区名称" prop="areaName" align="center"></el-table-column>
      <el-table-column label="分区等级" prop="levels" align="center"></el-table-column>
      <el-table-column label="上级分区名称" prop="pareaName" align="center"></el-table-column>
      <el-table-column label="操作" align="center">
        <template slot-scope="scope">
          <el-button type="primary" icon="el-icon-edit" @click="editorShow(scope.row)">编辑</el-button>
          <el-button type="danger" icon="el-icon-delete" @click="del(scope.row.areaCode)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
    <pagination
      :current-page="page.pageCurrent"
      :page-size="page.pageSize"
      :total="page.pageNum"
      @sizeChange="sizeChange"
      @currentChange="currentChange"
    ></pagination>
    <el-dialog
      title="店铺分区修改"
      :visible.sync="editorVisabilty"
      width="30%"
      :modal-append-to-body="false"
    >
      <el-form>
        <el-form-item label="分区名称">
          <el-input v-model="editorItem.areaName"></el-input>
        </el-form-item>
        <el-form-item label="分区等级">
          <el-input v-model="editorItem.levels" disabled></el-input>
        </el-form-item>
        <el-form-item label="分区ID">
          <el-input v-model="editorItem.areaCode" disabled></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="close">取 消</el-button>
        <el-button type="primary">确 定</el-button>
      </div>
    </el-dialog>
    <el-dialog
      title="店铺分区修改"
      :visible.sync="treeVisablity"
      width="550px"
      :modal-append-to-body="false"
    >
      <div class="block">
        <el-tree :data="treeData" node-key="label" default-expand-all :expand-on-click-node="false">
          <span class="custom-tree-node" slot-scope="{ node, data }">
            <span>{{ node.label }}</span>
            <span>
              <template v-if="node.level != 4">
                <el-button type="text" size="mini" @click="oper(data,0)">添加子分区</el-button>
              </template>
              <el-button type="text" size="mini" @click="oper(data,1)">修改</el-button>
              <el-button type="text" size="mini" @click="oper(data)">删除</el-button>
            </span>
          </span>
        </el-tree>
      </div>
    </el-dialog>
    <el-dialog
      title="编辑"
      :visible.sync="operTree"
      width="550px"
      center
      :modal-append-to-body="false"
    >
      <el-form :model="operTreeData" ref="operTreeData">
        <el-form-item>
          <el-input v-model="operTreeData.label">
            <template slot="prepend">分区名称</template>
          </el-input>
        </el-form-item>
      </el-form>
      <div slot="footer">
        <el-button @click="treeOper" type="success">提交</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {
  storePartitionList,
  storePartitionTabList,
  storePartitionAdd,
  storePartitionDel,
  storePartitionSearch,
  storePartitionUpdate
} from "@/api/index.js";
import pagination from "@/components/pagination";
export default {
  components: {
    pagination
  },
  data() {
    return {
      operTreeData: {},
      operTree: false,
      //树形图可见性
      treeVisablity: false,
      //树形数据
      treeData: [],
      //加载状态
      loading: false,
      //表格数据
      tableData: [],
      //搜索时临时数据
      tempData: {
        areaName: ""
      },
      //搜索时用的数据
      searchData: {},
      //分页数据
      page: {
        pageNum: null,
        pageCurrent: 1,
        pageSize: 5
      },
      editorItem: {},
      //编辑窗口分类可见性
      editorVisabilty: false,
      //新增窗口可见性
      addVisabilty: false,
      //新增店铺分区
      addItem: {}
    };
  },
  methods: {
    //初始化数据
    initData: function(page, size) {
      this.loading = true;
      storePartitionTabList({
        page: page || this.page.pageCurrent,
        size: size || this.page.pageSize,
        areaName: this.searchData.areaName
      }).then(res => {
        this.tableData = res.rows;
        this.page.pageNum = res.count;
      });
      setTimeout(() => {
        this.loading = false;
      }, 500);
    },
    //页数改变时的回调
    currentChange(val) {
      this.page.pageCurrent = val;
      this.initData();
    },
    //呼出编辑框
    editorShow(row) {
      this.$common.clearMessage(this.editorItem);
      this.editorItem = this.$common.deepCopy(row);
      this.editorVisabilty = true;
    },
    //删除店铺分区
    del(areaCode) {
      storePartitionDel({ areaCode }).then(res => {
          this.initData();
      });
    },
    close() {
      this.editorVisabilty = false;
      this.addVisabilty = false;
      this.operTreeData = {};
    },
    searchList: function() {
      this.searchData = this.$common.deepCopy(this.tempData);
      this.page.pageCurrent = 1;
      this.initData(1);
    },
    //每页总数改变时的回调
    sizeChange(val) {
      this.page.pageSize = val;
      this.initData(1);
    },
    initTree() {
      this.treeVisablity = true;
      storePartitionList().then(res => {
          this.treeData = res;
      });
    },
    oper(data, num) {
      if (num === 1) {
        this.flag = num;
        this.operTreeData = this.$common.deepCopy(data);
        this.operTree = true;
      } else if (num === 0) {
        this.flag = num;
        this.operTreeData = this.$common.deepCopy(data);
        this.operTreeData.label = "";
        this.operTree = true;
      } else {
        this.$confirm("此操作将永久删除分区信息,是否继续?", "警告", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
        .then(()=>{
          storePartitionDel({ areaCode: data.id }).then(res => {
              this.initTree();
          })}
        )
      }
    },
    treeOper() {
      if (this.flag === 1) {
        storePartitionUpdate({
          areaCode: this.operTreeData.id,
          areaName: this.operTreeData.label
        }).then(res => {
            this.operTree = false;
            this.initTree()
        });
      } else {
        storePartitionAdd({
          pareaCode: this.operTreeData.id,
          levels: this.operTreeData.level + 1,
          areaName: this.operTreeData.label
        }).then(res => {
            this.operTree=false
            this.initTree()
        });
      }
    }
  },
  beforeMount() {
    this.initData();
  }
};
</script>

<style lang="scss">
body {
  background: #fff;
}
.dialog-footer {
  text-align: center;
}
</style>




